<template>
  <section id="Account">
    <v-container>
      <v-alert v-if="!firstName">
        <v-row justify="center">
          <v-col md="4" style="max-width: 370px">
            <v-btn class="text-capitalize" color="primary" block>Log in</v-btn>
          </v-col>
          <v-col md="4" style="max-width: 370px">
            <v-btn class="text-capitalize" color="#16bf82" block
              >Create Account</v-btn
            >
          </v-col>
        </v-row>
      </v-alert>
      <v-row>
        <v-col>
          <v-card max-width="500" class="mx-auto" outlined>
            <v-card-text>
              <v-form>
                <v-card-title class="subtitle-1 text-sm-h6"
                  >Account information</v-card-title
                >
                <v-card-text>
                  <v-text-field
                    outlined
                    disabled
                    label="First Name"
                    v-model="firstName"
                  ></v-text-field>
                  <v-text-field
                    v-model="lastName"
                    outlined
                    disabled
                    label="Last Name"
                  ></v-text-field>
                  <v-text-field
                    outlined
                    v-model="email"
                    disabled
                    label="Email"
                  ></v-text-field>

                  <v-text-field
                    outlined
                    v-model="password"
                    disabled
                    label="Password"
                  ></v-text-field>
                </v-card-text>
              </v-form>
            </v-card-text>
          </v-card>
        </v-col>
      </v-row>
    </v-container>
  </section>
</template>

<script>
export default {
  data: () => ({
    firstName: "",
    lastName: "",
    email: "",
    password: "",
  }),
  mounted() {
    let user = localStorage.getItem("user-info");
    if (user) {
      this.firstName = JSON.parse(user).firstName;
      this.lastName = JSON.parse(user).lastName;
      this.email = JSON.parse(user).email;
      this.password = JSON.parse(user).password;
    }
  },
};
</script>
